.#{$rt-namespace}__close-button {
  color: #fff;
  background: transparent;
  outline: none;
  border: none;
  padding: 0;
  cursor: pointer;
  opacity: 0.7;
  transition: 0.3s ease;
  align-self: flex-start;

  &--light {
    color: #000;
    opacity: 0.3;
  }

  &>svg {
    fill: currentColor;
    display: block;
    height: 20px;
    width: 32px;
    margin-left: -1vw;
  }

  &:hover,
  &:focus {
    opacity: 1;
  }
}

.Toastify__close-button>svg {
  fill: currentColor;
  height: 16px;
  width: 32px;
  display: block;
  margin-left: -1vw;
}